import uniAdd from '../../components/uni-add/uni-add.vue';
export default {
	components: {
		uniAdd
	},
	data() {
		return {
			userList: [],
			colorArr: ['#409EFF', '#67C23A', '#00FFFF', '#F08080', '#FFE4E1', '#FFC0CB', '#F4A460', '#9ACD32', '#87CEFA', '#FF7F50']
		};
	},
	computed: {
		getColorIndex(inex) {
			let r, g, b;
			r = Math.floor(Math.random() * 256);
			g = Math.floor(Math.random() * 256);
			b = Math.floor(Math.random() * 256);
			return 'rgb(' + r + ',' + g + ',' + b + ')';
		}
	},
	created() {
		this.getList(0);
	},
	methods: {
		async getList(num) {
			const data = await this.$request({
				url: `AppCustomer/List/${num}`
			});
			this.userList = JSON.parse(data);
		},
		addBtnClick() {
			uni.navigateTo({
				url: `/pages/customerAdd/customerAdd`
			});
		},
		handleClickDetails(id) {
			uni.navigateTo({
				url: `/pages/customerDetails/customerDetails?id=${id}`
			});
		}
	}
};
